import * as React from "react";

import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import Container from "@mui/material/Container";
import Typography from "../../components/Typography";

const item = {
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  px: 5,
};

function ProductValues() {
  return (
    <Container sx={{ mt: 1, mb: 1, display: "flex", position: "relative" }}>
      <Grid container spacing={5}>
        <Grid item xs={12} md={12}>
          <Box sx={item} id="Roadmap">
            <Typography variant="h6" sx={{ my: 5 }}>
              About the collection
            </Typography>
            <Typography variant="h5" align="justify">
              {
                "It should be ensured that people have access to the information they need to be included in this period, where many people have little knowledge and interest, and have difficulty in expressing themselves, but extending from coins to the metaverse and which is one of the necessities of the new age. It seems very difficult to get everyone involved. But what is clear is that in order to involve everyone, it must inform them and make them realize that anything that seems complicated is actually simple and that anyone can do it."
              }
              <p>
                {
                  "The NFt world is just the beginning. Sometimes long-term, sometimes short-term responses can be received. While trying to obtain what is known and seen in a short time carries a great risk of loss, good feedback can be obtained as the holder."
                }
              </p>
              <p>
                {
                  "The nfts that we currently create in polygon and solana networks will gradually continue to spread in other networks. Instead of just creating or selling them, all the production stages will be published via all social media for those who are interested or want to learn about it."
                }
              </p>
              <p>
                {
                  "This collection of ours is created in the solana network. With the Mint transaction, you can buy it here, as well as on Opensea, the largest marketplace."
                }
              </p>
              <p>
                {
                  "You can reach our social media accounts below. You can ask questions and request support. Everything to improve and evolve!!!"
                }
              </p>
            </Typography>
          </Box>
        </Grid>
      </Grid>
    </Container>
  );
}

export default ProductValues;
