import * as React from "react";
import { styled } from "@mui/material/styles";
import Box from "@mui/material/Box";
import ButtonBase from "@mui/material/ButtonBase";
import Container from "@mui/material/Container";
import Typography from "../../components/Typography";

const ImageBackdrop = styled("div")(({ theme }) => ({
  position: "absolute",
  left: 0,
  right: 0,
  top: 0,
  bottom: 0,
  background: "#000",
  opacity: 0.5,
  transition: theme.transitions.create("opacity"),
}));

const ImageIconButton = styled(ButtonBase)(({ theme }) => ({
  position: "relative",
  display: "block",
  padding: 0,
  borderRadius: 0,
  height: "40vh",
  [theme.breakpoints.down("md")]: {
    width: "100% !important",
    height: 100,
  },
  "&:hover": {
    zIndex: 1,
  },
  "&:hover .imageBackdrop": {
    opacity: 0.15,
  },
  "&:hover .imageMarked": {
    opacity: 0,
  },
  "&:hover .imageTitle": {
    border: "4px solid currentColor",
  },
  "& .imageTitle": {
    position: "relative",
    padding: `${theme.spacing(2)} ${theme.spacing(4)} 14px`,
  },
  "& .imageMarked": {
    height: 3,
    width: 18,
    background: theme.palette.common.white,
    position: "absolute",
    bottom: -2,
    left: "calc(50% - 9px)",
    transition: theme.transitions.create("opacity"),
  },
}));

const images = [
  {
    url: "https://arweave.net/YQY4XBPzc7iDtehMTRu9pvJLx0JExMjoa2yaQha1uPE?ext=png&auto=format&fit=crop&w=400",
    title: "Anime Core #752",
    width: "40%",
  },
  {
    url: "https://arweave.net/DcvprtM0YF8X6rvdgp9b3m99S6eqRioK2SOTzPIPwjc?ext=png",
    title: "Anime Core #705",
    width: "20%",
  },
  {
    url: "https://gateway.pinata.cloud/ipfs/QmcPrv829fQLqYW6vk5CqnRw2nXaHj5UqVGjsvCX3GqJZe/1461.png",
    title: "Apes in Summer Uniques #1461",
    width: "40%",
  },
  {
    url: "https://gateway.pinata.cloud/ipfs/QmcPrv829fQLqYW6vk5CqnRw2nXaHj5UqVGjsvCX3GqJZe/1355.png",
    title: "Apes in Summer Uniques #1355",
    width: "38%",
  },
  {
    url: "https://gateway.pinata.cloud/ipfs/QmcPrv829fQLqYW6vk5CqnRw2nXaHj5UqVGjsvCX3GqJZe/1376.png",
    title: "Apes in Summer Uniques #1376",
    width: "38%",
  },
  {
    url: "https://arweave.net/XYNU2RhrRRbkaVAXUXRDo0lcS_fhIly4KBHm76xO3u8?ext=png",
    title: "Anime Core #369",
    width: "24%",
  },
  {
    url: "https://gateway.pinata.cloud/ipfs/QmcPrv829fQLqYW6vk5CqnRw2nXaHj5UqVGjsvCX3GqJZe/1566.png",
    title: "Apes in Summer Uniques #1566",
    width: "40%",
  },
  {
    url: "https://arweave.net/h6uBaxO7i544SkLj1-es5KX5wzjbW9pDJ9K25S0bj0Q?ext=png",
    title: "Anime Core #66",
    width: "20%",
  },
  {
    url: "https://gateway.pinata.cloud/ipfs/QmcPrv829fQLqYW6vk5CqnRw2nXaHj5UqVGjsvCX3GqJZe/1367.png",
    title: "Apes in Summer Uniques #1367",
    width: "40%",
  },
];

export default function ProductCategories() {
  return (
    <Container component="section" sx={{ mt: 8, mb: 4 }}>
      <Typography variant="h4" marked="center" align="center" component="h2">
        Random items from our collections.
      </Typography>
      <Box sx={{ mt: 8, display: "flex", flexWrap: "wrap" }} id="Collections">
        {images.map((image) => (
          <ImageIconButton
            key={image.title}
            style={{
              width: image.width,
            }}
          >
            <Box
              sx={{
                position: "absolute",
                left: 0,
                right: 0,
                top: 0,
                bottom: 0,
                backgroundSize: "cover",
                backgroundPosition: "center 40%",
                backgroundImage: `url(${image.url})`,
              }}
            />
            <ImageBackdrop className="imageBackdrop" />
            <Box
              sx={{
                position: "absolute",
                left: 0,
                right: 0,
                top: 0,
                bottom: 0,
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                color: "common.white",
              }}
            >
              <Typography
                component="h3"
                variant="h6"
                color="inherit"
                className="imageTitle"
              >
                {image.title}
                <div className="imageMarked" />
              </Typography>
            </Box>
          </ImageIconButton>
        ))}
      </Box>
    </Container>
  );
}
